import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function forwardPriceToEarningsGrowthRatioTtm(tableName: string) {
  const title = "Forward PEG (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["forwardPriceToEarningsGrowthRatioTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Projected PEG ratio based on future earnings estimates.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about forward PEG or other valuation measures?`,
              value:
                info.row.original.financialData.companyRatios?.data
                  ?.forwardPriceToEarningsGrowthRatioTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            info.row.original.financialData.companyRatios?.data
              ?.forwardPriceToEarningsGrowthRatioTtm
          }
        />
      )
    },
  })
}

export { forwardPriceToEarningsGrowthRatioTtm }
